<template>
  <div v-if="loaded" class="app__wrap">
      <main class="app-content">
        <app-bar @toggleAi="toggleAiVisibility"></app-bar>
        <transition name="fade" mode="out-in">
          <router-view class="page-content"></router-view>
        </transition>
        <footer-bar></footer-bar>
        <ai-agent-button v-if="shouldShowAiAgent" />
      </main>
    
    <!-- Domain Migration Popup -->
    <domain-migration-popup 
      v-if="showDomainMigrationPopup && isOldDomain" 
      @close="closeDomainMigrationPopup"
    />
    
    <!-- Migration Banner -->
    <migration-banner v-if="isOldDomain" />
  </div>
</template>

<script setup>
  import {onMounted, ref, defineExpose, defineAsyncComponent, computed, watch} from 'vue';
  import { useRoute, useRouter } from 'vue-router';
  import store from './vuex';
  import 'solana-wallets-vue/styles.css'
  
  // Заменяем прямые импорты на ленивую загрузку для тяжелых компонентов
  const AppBar = defineAsyncComponent(() => import('./components/AppBar'));
  const FooterBar = defineAsyncComponent(() => import('./components/FooterBar'));
  const AiAgentButton = defineAsyncComponent(() => import('./components/ai/AiAgentButton'));
  const DomainMigrationPopup = defineAsyncComponent(() => import('./components/DomainMigrationPopup'));
  const MigrationBanner = defineAsyncComponent(() => import('./components/MigrationBanner'));
  
  import {
    // LedgerWalletAdapter,
    PhantomWalletAdapter,
    // SlopeWalletAdapter,
    // TorusWalletAdapter,
  } from '@solana/wallet-adapter-wallets';
  import { initWallet } from 'solana-wallets-vue';

  const route = useRoute()
  const router = useRouter()

  const wallets = [
    new PhantomWalletAdapter(),
    // new SlopeWalletAdapter(),
    // new TorusWalletAdapter(),
    // new LedgerWalletAdapter(),
  ];

  initWallet({ wallets, autoConnect: store.state.autoConnect});

  const loaded = ref(false);
  const showAiAgent = ref(true) // Default to true
  const pageScrollPosition = ref(0);
  const showDomainMigrationPopup = ref(false);
  
  // Проверяем текущий домен
  const isOldDomain = computed(() => {
    return window.location.hostname === 'solanabitcat.com'
  });
  
  // Computed property to check if AI agent should be shown
  const shouldShowAiAgent = computed(() => {
    return showAiAgent.value && route.path !== '/ai'
  })
  
  onMounted(() => {
    console.log('App mounted, checking for referral code');
    
    // Проверяем наличие параметра r в URL
    let ref = route.query.r;
    console.log('Referral from route.query:', ref);
    
    // Проверяем в хэш-части URL
    if (!ref && window.location.hash.includes('?r=')) {
      const hashPart = window.location.hash;
      console.log('Hash part contains ?r=', hashPart);
      
      // ===================== НОВАЯ РЕАЛИЗАЦИЯ (РАБОЧАЯ) =====================
      // Разделяем хэш на путь и параметры
      const [path, queryString] = hashPart.split('?');
      
      if (queryString) {
        // Создаем объект URLSearchParams для работы с параметрами
        const params = new URLSearchParams(queryString);
        
        // Получаем значение параметра r
        ref = params.get('r');
        console.log('Extracted referral from hash params:', ref);
        
        if (ref) {
          // Удаляем параметр r
          params.delete('r');
          
          // Создаем новый URL
          const newQueryString = params.toString();
          const newHash = newQueryString 
            ? `${path}?${newQueryString}` 
            : path;
          
          console.log('New hash after removing referral:', newHash);
          
          // Используем router.replace вместо прямого изменения хэша
          // Удаляем начальный символ # из пути
          const routePath = newHash.startsWith('#') ? newHash.substring(1) : newHash;
          console.log('Replacing route with:', routePath);
          router.replace(routePath);
          
          // Добавляем небольшую задержку, чтобы убедиться, что реферальный код сохранен
          setTimeout(() => {
            if (window.location.hash.includes('?r=')) {
              console.log('Referral parameter still present, forcing clean URL');
              const cleanPath = window.location.hash.split('?')[0];
              router.replace(cleanPath.substring(1));
            }
          }, 100);
        }
      }
      // ===================== КОНЕЦ НОВОЙ РЕАЛИЗАЦИИ =====================
      
      // ===================== СТАРАЯ РЕАЛИЗАЦИЯ (ЗАКОММЕНТИРОВАНА) =====================
      // // Извлекаем значение параметра r
      // const rIndex = hashPart.indexOf('?r=');
      // if (rIndex !== -1) {
      //   const startIndex = rIndex + 3; // Длина '?r='
      //   const endIndex = hashPart.indexOf('&', startIndex);
      //   ref = endIndex !== -1 
      //     ? hashPart.substring(startIndex, endIndex) 
      //     : hashPart.substring(startIndex);
      //   
      //   console.log('Extracted referral from hash:', ref);
      //   
      //   // Удаляем параметр r из URL
      //   let newHash;
      //   if (endIndex !== -1) {
      //     // Если есть другие параметры после r
      //     newHash = hashPart.substring(0, rIndex) + '?' + hashPart.substring(endIndex + 1);
      //   } else {
      //     // Если r - единственный параметр
      //     newHash = hashPart.substring(0, rIndex);
      //   }
      //   
      //   // Удаляем лишний знак вопроса, если нет других параметров
      //   if (newHash.endsWith('?')) {
      //     newHash = newHash.substring(0, newHash.length - 1);
      //   }
      //   
      //   console.log('New hash after removing referral:', newHash);
      //   window.history.replaceState(null, '', newHash);
      // }
      // ===================== КОНЕЦ СТАРОЙ РЕАЛИЗАЦИИ =====================
    }
    
    // Проверяем в основной части URL
    if (!ref) {
      const urlParams = new URLSearchParams(window.location.search);
      ref = urlParams.get('r');
      console.log('Referral from search params:', ref);
      
      if (ref) {
        // Удаляем параметр r из URL
        urlParams.delete('r');
        const newSearch = urlParams.toString() ? `?${urlParams.toString()}` : '';
        const newUrl = `${window.location.pathname}${newSearch}${window.location.hash}`;
        console.log('New URL after removing referral:', newUrl);
        window.history.replaceState(null, '', newUrl);
      }
    }
    
    // Если параметр найден, сохраняем его в localStorage
    if (ref && !store.getters.isAuthenticated) {
      localStorage.setItem('ref', ref);
      console.log('Referral code saved to localStorage:', ref);
    }
    loaded.value = true;
    console.log('App mounted');
    // console.log(window.location.hash.startsWith('#/'));
    // console.log(window.location.hash)

    // check path and if /#/path redirect to /path
    // if (window.location.hash.startsWith('#/')) {
    //   router.push(window.location.hash.replace('#/', '/'));
    // }

    const aiEnabled = localStorage.getItem('bitcat_ai_enabled')
    showAiAgent.value = aiEnabled === null ? true : aiEnabled === 'true'
    window.addEventListener('scroll', setPageScrollPosition);
    window.addEventListener("beforeunload", handleBeforeUnload);
    
    // Показываем popup о переходе на новый домен через 10 секунд
    // Но только если он не был показан в этой сессии и мы на старом домене
    if (isOldDomain.value) {
      const domainPopupShown = sessionStorage.getItem('domain_migration_popup_shown')
      if (!domainPopupShown) {
        setTimeout(() => {
          showDomainMigrationPopup.value = true
          sessionStorage.setItem('domain_migration_popup_shown', 'true')
        }, 10000) // 10 секунд
      }
    }
  })

  function handleBeforeUnload() {
    if(window.solana) {
      window.solana.disconnect()
    }
  }

  function setPageScrollPosition() {
    pageScrollPosition.value = window.scrollY;
  }

  const toggleAiVisibility = (value) => {
    showAiAgent.value = value
    localStorage.setItem('bitcat_ai_enabled', value.toString())
  }

  const closeDomainMigrationPopup = () => {
    showDomainMigrationPopup.value = false
  }

  defineExpose({
    toggleAiVisibility
  })

  console.log('Welcome to BITCAT Universe dApp! Enjoy =)');
</script>

